<template>
    <div class="container">
        <p class="status error" v-if="error">{{error}}</p>
        <p class="status" v-if="status">{{status}}</p>
        <div v-if="posts.length === 0" class="post-date">
            <p class="post-date">No messages yet</p>
        </div>
        <div class="posts-container">
            <div v-for="(post,index) in posts"
                 v-bind:item="post"
                 v-bind:index="index"
                 v-bind:key="post._id">
                <div v-if="mustShowDate(index)" class="post-date">
                    <p class="post-date">{{post.time.toLocaleDateString()}}</p>
                </div>
                <div class="row post" v-bind:class="{mine: userId===post.user.id}">
                    <div class="avatar">
                        <img class="avatar" :src="post.user.publicData.avatar"/>
                    </div>
                    <div class="post-balloon" v-bind:class="{mine: userId===post.user.id, temp:post.temp}">
                        <p class="user-name" v-bind:class="{mine: userId===post.user.id}">
                            {{post.user.publicData.displayName}}</p>
                        <p class="post-time" v-bind:class="{mine: userId===post.user.id}">
                            {{post.time.toLocaleTimeString()}}</p>
                        <p class="text">{{post.text}}</p>
                    </div>
                </div>
            </div>
        </div>

        <div class="chat">
            <p v-if="getTypingText() !== null" class="typing">{{getTypingText()}}</p>
            <div class="row">
                <b-input class="chat" v-model="text" ref="chat_txt"
                         v-on:submit.prevent="sendMessage"
                         v-on:keypress.enter="sendMessage" type="text"
                         placeholder="Say something ..."/>
                <b-button class="chat" is-text v-on:click="sendMessage">
                    <img class="chat" src="../assets/send.png"/>
                </b-button>
            </div>
        </div>

    </div>
</template>

<script>
    import SocketService from "../services/SocketService";

    const crypto = require("crypto");

    export default {
        name: 'ChatComponent',
        data() {
            return {
                token: localStorage.getItem('user-token'),
                userId: null,
                posts: [],
                error: '',
                status: '',
                text: '',
                lastScrollY: 0,
                isTyping: false,
                users: null
            }
        },
        async created() {
            try {
                this.start();
            } catch (e) {
                this.error = e.message;
            }

        },
        watch: {
            text() {
                this.isTyping = this.text.length > 0;
            },
            isTyping() {
                this.updateState({"is_typing": this.isTyping})
            }
        },
        methods: {
            start() {
                this.status = "Connecting ...";

                if (this.token !== null) {
                    this.initChat();
                } else {
                    localStorage.removeItem('user-token');
                    this.$emit("setToken", null);
                }
            },
            initChat() {
                if (!SocketService.client) {
                    SocketService.connect();
                }

                SocketService.client.handleEvent(SocketService.client.EventHandlers.OnConnect,
                    () => {
                        SocketService.client.loginWithToken(this.token)
                    });

                SocketService.client.handleEvent(SocketService.client.EventHandlers.OnDisconnect,
                    () => {
                        console.log("DC");
                        this.start();
                    });

                SocketService.client.handleEvent(SocketService.client.EventHandlers.OnLogin,
                    (token) => {
                        if (token === null) {
                            localStorage.removeItem('user-token');
                            this.$emit("setToken", null);
                        } else {
                            this.setupHandlers();
                        }
                    });
            },
            setupHandlers() {
                SocketService.client.listen("init_data", (data) => {
                    this.status = "Updating ...";
                    this.userId = data.userId;
                    this.users = data.users;
                });

                SocketService.client.listen("update", (data) => {
                    this.status = "";

                    this.posts = data.map((message) => ({
                        ...message,
                        time: new Date(message.time)
                    })).reverse();


                    this.$nextTick(() => {
                        this.scrollToEnd(false);
                    });
                });

                SocketService.client.listen("update_user_data", (data) => {
                    this.users[data.id] = data;
                    this.$forceUpdate();
                });

                SocketService.client.listen("message", (message) => {
                    message = {
                        ...message,
                        time: new Date(message.time)
                    };

                    let edited = false;
                    this.posts = this.posts.map((post) => {
                        if (post.uid === message.uid) {
                            edited = true;
                            message.temp = false;
                            return message;
                        } else {
                            return post;
                        }
                    });

                    if (!edited) {
                        this.posts.push(message);
                        this.$nextTick(() => {
                            this.scrollToEnd(true);
                        });
                        this.showNotif(message.text);
                    }
                });
            },
            scrollToEnd(smooth) {
                window.scrollTo(
                    {
                        top: document.body.scrollHeight || document.documentElement.scrollHeight,
                        left: 0,
                        behavior: smooth ? "smooth" : "auto"
                    });
            },
            scrollTo(val, smooth) {
                window.scrollTo(
                    {
                        top: val,
                        left: 0,
                        behavior: smooth ? "smooth" : "auto"
                    });
            },
            async sendMessage() {
                this.$refs.chat_txt.focus();

                if (this.text.length === 0)
                    return;

                this.lastScrollY = window.scrollY;

                let message = {
                    uid: crypto.randomBytes(16).toString("hex"),
                    user: this.users[this.userId],
                    text: this.text,
                    time: new Date(),
                    temp: true
                };

                this.text = "";
                this.posts.push(message);

                setTimeout(async () => {
                    delete message.user;
                    SocketService.client.say("message", message)
                }, 200);

                this.$nextTick(() => {
                    this.scrollToEnd(true);
                });
            },
            mustShowDate(idx) {
                if (idx === 0)
                    return true;

                return this.posts[idx].time.toLocaleDateString()
                    !== this.posts[idx - 1].time.toLocaleDateString();
            },
            updateState(state) {
                SocketService.client.say("update_state", state);
            },
            getTypingText() {
                if (!this.users)
                    return null;

                let count = 0;
                let names = '';
                let c = 0;
                const max = Object.keys(this.users).length;
                for (let id in this.users) {
                    c++;

                    try {
                        if (id === this.userId)
                            continue;
                        if (this.users[id].state.is_typing) {
                            names += this.users[id].publicData.displayName;
                            if (c < max - 1)
                                names += ", ";
                            count++;
                        }
                    } catch (e) {
                        //do nothing
                    }
                }

                if (count === 0)
                    return null;

                names += (count > 1 ? " are " : " is ") + "typing ...";

                return names;
            },
            showNotif(message) {
                this.checkNotifPermission(() => {
                    new Notification(message);
                });
            },
            checkNotifPermission(onGranted) {
                if (!("Notification" in window)) {
                    return;
                }

                if (Notification.permission === "granted") {
                    onGranted();
                } else if (Notification.permission !== "denied") {
                    Notification.requestPermission().then(function (permission) {
                        if (permission === "granted") {
                            onGranted();
                        }
                    });
                }
            }

        }
    }
</script>

